body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

body{
  background-color: #9266ff;
}

#to-do-form input {
background-color: rgb(27, 112, 137);
border: 0;
width: 200px;
height: 50px;
padding: 0 20px;
margin: 20px;
font-size: 18px;
border-radius: 18px;
color: #fff;
}

#to-do-form input::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.5)
}

#to-do-form input:-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.5)
}

#to-do-form input::placeholder {
  color: rgba(255, 255, 255, 0.5)
}

#to-do-form input:focus {
outline: none;
}

#to-do-form button {
  width: 80px;
  height: 50px;
  border: 0;
  border-radius: 5px;
  cursor: pointer;
  background-color: rgb(202, 156, 24);
  font-weight: bolder;
  color: #fff;
}


.list{
    color:#fff;
    width: 300px;
    background-color: #bf07ac;
    height: 50px;
    margin: 20px auto;
    border-radius: 5px;
}

.list p{
    padding: 10px;
    position: relative;
}

.list p span {
    position: absolute;
    right: 10px;
    cursor: pointer;
  }

  .list p input{
      background-color: transparent;
      border: 0;
      color:#fff;
      font-size: 18px;
  }

  .list p input:focus{
      outline:none;
  }
