.App {
  background-color: #3f3160;
  min-height: 500px;
  width: 400px;
  margin: 40px auto;
}

body{
  background-color: #9266ff;
}

#to-do-form input {
background-color: rgb(27, 112, 137);
border: 0;
width: 200px;
height: 50px;
padding: 0 20px;
margin: 20px;
font-size: 18px;
border-radius: 18px;
color: #fff;
}

#to-do-form input::placeholder {
  color: rgba(255, 255, 255, 0.5)
}

#to-do-form input:focus {
outline: none;
}

#to-do-form button {
  width: 80px;
  height: 50px;
  border: 0;
  border-radius: 5px;
  cursor: pointer;
  background-color: rgb(202, 156, 24);
  font-weight: bolder;
  color: #fff;
}

